// @flow
import React, { useState, Fragment } from 'react';
import type { Element } from 'react';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import _get from 'lodash/get';
import { isDisableButton } from 'helpers/Offer';
import { APP_BASE_URL, OFFER_STEPS } from 'shared/Consts';

type Props = {
  cell: Object,
  handleApproval: Function,
  isSubmitting: boolean,
  handleDecline: Function,
};

export const ActionButtons = ({
  cell,
  handleApproval,
  isSubmitting,
  handleDecline,
}: Props): Element<typeof Fragment> => {
  const [anchorEl, setAnchorEl] = useState(null);
  const offer = _get(cell, ['row', 'original']);
  const offerId = _get(offer, ['OfferId']);
  const formId = _get(offer, ['Forms', 0, 'FormId']);

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const isInvalidSubmission =
    isSubmitting || isDisableButton(offer, OFFER_STEPS.awaitingSellerAction);

  const onClickApprovalHandler = () => {
    if (isInvalidSubmission) return;

    handleApproval(offerId);
    handleClose();
  };

  const onClickDeclineHandler = () => {
    if (isInvalidSubmission) return;

    handleDecline(offerId);
    handleClose();
  };

  return (
    <>
      <button
        type="button"
        className="minimal-btn animated_fastest"
        onClick={handleClick}
        disabled={isInvalidSubmission}
      >
        <i className="fa fa-ellipsis-h"></i>
      </button>
      <Menu
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <MenuItem component="button" onClick={onClickApprovalHandler}>
          Accept
        </MenuItem>
        <MenuItem component="button" onClick={onClickDeclineHandler}>
          Decline
        </MenuItem>
        <MenuItem
          component="a"
          href={`${APP_BASE_URL}/offer?offerId=${offerId}&step=${OFFER_STEPS.sellerCounter}&formId=${formId}`}
        >
          Counter
        </MenuItem>
      </Menu>
    </>
  );
};
